//  
export function deleteSceneObjects(scene) {
  for (let i = scene.children.length - 1; i >= 0; i--) {
    const obj = scene.children[i];

    if ((obj instanceof THREE.Mesh || obj instanceof THREE.Line || obj instanceof THREE.Group)) {
      scene.remove(obj);
    }
  }
}




